"use strict";


import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';
import ContainerNamespace from "./ContainerNamespace";
import {systemDataCollector} from "../../services/SystemDataCollector";

export class ProcessStateNamespace extends Namespace {
    callMethod(name, args) {
        if (name === "dump") {
            return this.dump(args);
        }
        return new ContainerNamespace();
    }

    dump() {
        let usage = systemDataCollector.getLastUsage();

        if (!usage) {
            return new ContainerNamespace();
        }

        return new ContainerNamespace({
            "CPU": new JSObjectNamespace(usage.cpu),
            "GlobalCPU": new JSObjectNamespace(usage.globalCpu),
            "VirtualMemorySize": new JSObjectNamespace(usage.virtualMemory),
            "ProcessStartTime": new JSObjectNamespace(usage.startTime),
            "SystemUpTime": new JSObjectNamespace(usage.uptime),
        });
    }
}

export const ProcessStateNamespaceInstance = new ProcessStateNamespace();
